import Swiper from "swiper";

document.addEventListener("DOMContentLoaded", (event) => {
    const filterBoxes = document.querySelectorAll('.filter-sort');
    if (filterBoxes) {
        filterBoxes.forEach(filterBox => {
            const filterBoxTop = filterBox.querySelector('.filter-sort__current');
            const filterBoxList = filterBox.querySelector('.filter-sort__body');
            const clickOutBoxHandler = ({target}) => {
                if (!target.classList.contains('filter-sort') && !target.closest('.filter-sort')) {
                    filterBoxList.removeAttribute('data-active');
                    document.removeEventListener('click', clickOutBoxHandler);
                }
            };

            filterBoxTop.addEventListener('click', (evt) => {
                if (filterBoxList.getAttribute('data-active')) {
                    filterBoxList.removeAttribute('data-active');
                    document.removeEventListener('click', clickOutBoxHandler);
                } else {
                    filterBoxes.forEach(flBox => {
                        const flBoxList = flBox.querySelector('.filter-sort__body');
                        flBoxList.removeAttribute('data-active');
                    });
                    filterBoxList.setAttribute('data-active', true);
                    document.addEventListener('click', clickOutBoxHandler);
                }
            });

            const checkboxes = [...filterBox.querySelectorAll('input[type="checkbox"]')];
            let atLeastOneChecked = false;
            atLeastOneChecked = checkboxes.find(el => el.checked === true);
            if (!!atLeastOneChecked) {
                filterBox.setAttribute('data-inner-checked', true);
            } else {
                filterBox.removeAttribute('data-inner-checked');
            }

            filterBox.addEventListener('change', (evt) => {
                const checkboxes = [...filterBox.querySelectorAll('input[type="checkbox"]')];
                let atLeastOneChecked = false;
                atLeastOneChecked = checkboxes.find(el => el.checked === true);
                if (!!atLeastOneChecked) {
                    filterBox.setAttribute('data-inner-checked', true);
                } else {
                    filterBox.removeAttribute('data-inner-checked');
                }
            });
        });
    }


    new Swiper(".orders-services__filters .swiper", {
        slidesPerView: "auto",
        spaceBetween: 12,
    });
});
