require('./scss/styles.scss');
import Swiper from 'swiper';
import {
    Thumbs,
    Grid,
    Navigation,
    Pagination,
    Autoplay,
    FreeMode,
    EffectCards,
    EffectFade,
    Controller
} from 'swiper/modules';
import Choices from "choices.js";
import 'choices.js/public/assets/styles/choices.min.css'
import {SeasonsHeader} from "./js/header";
import debounce from "lodash/debounce";
import 'js-circle-progress';
import * as catalog from "./js/catalog";
import * as profile from "./js/profile";
import * as lk from "./js/lk";
import Inputmask from "inputmask/lib/inputmask.js";

Swiper.use([Thumbs, Navigation, Pagination, Autoplay, FreeMode, EffectCards, EffectFade, Controller]);


document.addEventListener("DOMContentLoaded", (event) => {

        const header = document.querySelector('.header');
        new SeasonsHeader(header);

        const inputs = document.querySelectorAll("input");
        inputs.forEach(input => {
            input.addEventListener("focus", function () {
                // Добавляем класс при фокусировке
                this.closest('.form-input')?.setAttribute('data-focused', true);
            });
            input.addEventListener("blur", function () {
                // Удаляем класс при снятии фокуса
                this.closest('.form-input')?.removeAttribute('data-focused');
            });
        });


        const timerElements = document.querySelectorAll('.timer');
        if (timerElements.length) {
            timerElements.forEach(timerElement => {

                const timestamp = parseInt(timerElement.getAttribute('data-timestamp'), 10) * 1000;
                const dayElement = timerElement.querySelector('.day');
                const hourElement = timerElement.querySelector('.hour');
                const minElement = timerElement.querySelector('.min');
                const secElement = timerElement.querySelector('.sec');

                const dayLabelElement = timerElement.querySelector('.day-label');
                const hourLabelElement = timerElement.querySelector('.hour-label');
                const minLabelElement = timerElement.querySelector('.min-label');
                const secLabelElement = timerElement.querySelector('.sec-label');

                const getDeclension = (number, words) => {
                    number = Math.abs(number) % 100;
                    const lastDigit = number % 10;
                    if (number > 10 && number < 20) {
                        return words[2];
                    }
                    if (lastDigit > 1 && lastDigit < 5) {
                        return words[1];
                    }
                    if (lastDigit == 1) {
                        return words[0];
                    }
                    return words[2];
                }

                const updateTimer = () => {
                    const now = new Date().getTime();
                    const distance = timestamp - now;

                    if (distance < 0) {
                        dayElement.textContent = '0';
                        hourElement.textContent = '0';
                        minElement.textContent = '0';
                        secElement.textContent = '0';
                        dayLabelElement.textContent = 'ДНЕЙ';
                        hourLabelElement.textContent = 'ЧАС';
                        minLabelElement.textContent = 'МИН';
                        secLabelElement.textContent = 'СЕК';
                        clearInterval(interval);
                        return;
                    }

                    const days = Math.floor(distance / (1000 * 60 * 60 * 24));
                    const hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
                    const minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
                    const seconds = Math.floor((distance % (1000 * 60)) / 1000);

                    dayElement.textContent = days;
                    hourElement.textContent = hours;
                    minElement.textContent = minutes;
                    secElement.textContent = seconds;

                    dayLabelElement.textContent = getDeclension(days, ['ДЕНЬ', 'ДНЯ', 'ДНЕЙ']);
                    hourLabelElement.textContent = getDeclension(hours, ['ЧАС', 'ЧАСА', 'ЧАСОВ']);
                    minLabelElement.textContent = getDeclension(minutes, ['МИНУТА', 'МИНУТЫ', 'МИНУТ']);
                    secLabelElement.textContent = getDeclension(seconds, ['СЕКУНДА', 'СЕКУНДЫ', 'СЕКУНД']);
                }

                const interval = setInterval(updateTimer, 1000);
                updateTimer();
            })
        }

        new Swiper(".welcome .swiper", {
            spaceBetween: 0,
            slidesPerView: 1,
            navigation: {
                prevEl: '.welcome__navigation .seasons-nav__arrow--left',
                nextEl: '.welcome__navigation .seasons-nav__arrow--right',
            },
        });

        const sectionsWithProducts = document.querySelectorAll('.popular__content .swiper');
        if (sectionsWithProducts.length) {
            sectionsWithProducts.forEach(sectionWithProducts => {
                new Swiper(sectionWithProducts, {
                    grid: {
                        rows: 1, // Устанавливаем количество строк
                    },
                    spaceBetween: 0,
                    slidesPerView: "auto",
                    breakpoints: {
                        1241: {
                            grid: {
                                rows: 1, // Устанавливаем количество строк
                            },
                            spaceBetween: 16,
                            slidesPerView: 4,
                        },
                        768: {
                            slidesPerView: 2,
                            spaceBetween: 0,
                            grid: {
                                rows: 2, // Устанавливаем количество строк
                            },
                        },
                    }
                });
            });
        }

        function scrollToElementWithHeaderOffset(element, header) {
            const headerHeight = document.querySelector('header').offsetHeight; // Высота хедера
            const elementPosition = element.getBoundingClientRect().top; // Позиция элемента относительно верхней границы окна
            const offsetPosition = elementPosition - headerHeight; // Позиция с учетом высоты хедера

            // Прокручиваем страницу на нужное расстояние
            window.scrollBy({
                top: offsetPosition,
                behavior: 'smooth'
            });
        }

        const tabsBlock = document.querySelectorAll('[data-tabs]');
        if (tabsBlock.length) {
            tabsBlock.forEach(tabsBlock => {
                const btns = tabsBlock.querySelectorAll('[data-tab]');
                const blocks = tabsBlock.querySelectorAll('[data-tab-block]');
                for (let btn of btns) {
                    btn.addEventListener('click', () => {
                        const id = parseInt(btn.getAttribute('data-tab'));
                        for (let block of blocks) {
                            if (parseInt(block.getAttribute('data-tab-block')) === id) {
                                block.setAttribute('data-active', '');
                                scrollToElementWithHeaderOffset(block);
                            } else {
                                block.removeAttribute('data-active');
                            }
                        }
                        btn.setAttribute('data-active', '');
                        for (let otherBtn of btns) {
                            if (parseInt(otherBtn.getAttribute('data-tab')) !== id) {
                                otherBtn.removeAttribute('data-active');
                            }
                        }
                    });
                }
            });
        }

        const sectionsProducts = document.querySelectorAll('.section-products');
        if (sectionsProducts.length > 0) {
            sectionsProducts.forEach(sectionsProduct => {
                const id = sectionsProduct.getAttribute('data-tab-block');
                const swiper = document.querySelector(`[data-tab-block='${id}'] .swiper`)
                new Swiper(swiper, {
                    spaceBetween: 16,
                    slidesPerView: "auto",
                    navigation: {
                        prevEl: `[data-tab-block='${id}'] .seasons-nav__arrow--left`,
                        nextEl: `[data-tab-block='${id}'] .seasons-nav__arrow--right`,
                    },
                });
            })
        }

        const doProportionIcon = (icons) => {
            for (const icon of icons) {
                const widthAttr = icon.getAttribute('width');
                const widthReal = icon.clientWidth;
                const heightAttr = icon.getAttribute('height');
                const heightReal = (widthReal / widthAttr) * heightAttr;
                icon.style.height = `${heightReal}px`;
            }
        }


        const icons = document.querySelectorAll('.icon-pr');
        if (icons.length) {
            doProportionIcon(icons);
            window.addEventListener('resize', debounce(() => {
                doProportionIcon(icons);
            }, 300));
        }


        var swiper = new Swiper(".product-description__thumb-slider .swiper", {
            loop: true,
            spaceBetween: 8,
            slidesPerView: 3,
            direction: "horizontal",
            watchSlidesProgress: true,
            breakpoints: {
                768: {
                    spaceBetween: 12,
                    direction: "vertical",
                }
            }
        });
        var swiper2 = new Swiper(".product-description__big-slider .swiper", {
            loop: true,
            spaceBetween: 10,
            thumbs: {
                swiper: swiper,
            },
            navigation: {
                prevEl: '.product-description__big-slider .seasons-nav__arrow--left',
                nextEl: '.product-description__big-slider .seasons-nav__arrow--right',
            },
        });




        // temp
        const viewBoxxxx = document.querySelector('.delivery-box');
        if (viewBoxxxx) {
            const viewBoxItems = viewBoxxxx.querySelectorAll('.delivery-box__choice');

            viewBoxItems.forEach(viewBoxItem => {
                viewBoxItem.addEventListener('click', (evt) => {
                    if (!viewBoxItem.classList.contains('delivery-box__choice--active')) {
                        const activeCur = viewBoxxxx.querySelector('.delivery-box__choice--active');
                        if (activeCur) {
                            activeCur?.classList.remove('delivery-box__choice--active');
                        }
                        viewBoxItem.classList.add('delivery-box__choice--active');
                    }
                });
            })
        }


        new Swiper(".auth-banner__slider.swiper", {
            spaceBetween: 0,
            slidesPerView: 1,
            autoplay: {
                delay: 5000,
            },
            pagination: {
                el: '.auth-banner__pagination',
                type: 'bullets',
            },
        });

        const choices = document.querySelectorAll('.auth-body__choose [name="form-type"]');
        const choiceBoxes = document.querySelectorAll('.auth-body__choice[data-choice]');
        if (choices.length > 0 && choiceBoxes.length > 0 && choices.length === choiceBoxes.length
        ) {
            choices.forEach(choice => {
                if (choice.checked) {
                    for (let i = 0; i < choiceBoxes.length; i++) {
                        if (choiceBoxes[i].getAttribute('data-choice') === choice.value) {
                            choiceBoxes[i].setAttribute('data-choice-active', true);
                        } else {
                            choiceBoxes[i].removeAttribute('data-choice-active');
                        }
                    }
                }
                choice.addEventListener('change', (evt) => {
                    const id = evt.target.value;
                    for (let i = 0; i < choiceBoxes.length; i++) {
                        if (choiceBoxes[i].getAttribute('data-choice') === id) {
                            choiceBoxes[i].setAttribute('data-choice-active', true);
                        } else {
                            choiceBoxes[i].removeAttribute('data-choice-active');
                        }
                    }
                });
            });
        }


        const formSelectEls = document.querySelectorAll('.form-select select');
        if (formSelectEls.length) {
            formSelectEls.forEach(formSelectEl => {
                new Choices(formSelectEl, {
                    searchEnabled: false
                });
            })
        }
    }
);